* {
  box-sizing: border-box;
}

body {
  font: 100%/1.5 "Helvetica Neue", arial, sans-serif;
}

.container {
  max-width: 1200px;
  margin-right: auto;
  margin-left: auto;
  padding: 20px;
}

.container h1 {
  text-align: center;
  font-weight: normal;
}

@keyframes pulse {
  0% {
    background-color: rgba(19, 129, 161, 0.5);
  }
  100% {
    background-color: #1381a1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes show {
  0% {
    background-color: rgba(19, 129, 161, 0.5);
  }
  100% {
    background-color: #1381a1;
  }
}

.grid-7x7 {
  position: relative;
  overflow: hidden;
}

.grid-7x7 * {
  box-sizing: border-box;
}

.grid-7x7 .grid {
  background: #ccc;
  animation: fadeIn 3s 1;
  display: flex;
  flex-flow: row wrap;
  margin-right: -1px;
  margin-left: -1px;
}

.grid-7x7 .grid .grid-item {
  padding: 1px;
  outline: 1px solid rgba(255, 255, 255, 0.8);
  flex: 1 1 14.28571%;
}

.grid-7x7 .grid .grid-item:before {
  content: "";
  display: inline-block;
  padding-bottom: 100%;
}

.grid-7x7 .custom-items {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
}

.grid-7x7 .custom-items .custom-item {
  background-color: #1381a1;
  position: absolute;
  animation: show 10s ease-out 1;
}

.grid-7x7 .custom-items .custom-item.top0 {
  top: 0;
}

.grid-7x7 .custom-items .custom-item.left0 {
  left: 0;
}

.grid-7x7 .custom-items .custom-item.width7 {
  width: 100%;
}

